export function createShadowTokens(ambientColor, keyColor) {
  let tokenSuffix = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : '';
  return {
    ["shadow2".concat(tokenSuffix)]: "0 0 2px ".concat(ambientColor, ", 0 1px 2px ").concat(keyColor),
    ["shadow4".concat(tokenSuffix)]: "0 0 2px ".concat(ambientColor, ", 0 2px 4px ").concat(keyColor),
    ["shadow8".concat(tokenSuffix)]: "0 0 2px ".concat(ambientColor, ", 0 4px 8px ").concat(keyColor),
    ["shadow16".concat(tokenSuffix)]: "0 0 2px ".concat(ambientColor, ", 0 8px 16px ").concat(keyColor),
    ["shadow28".concat(tokenSuffix)]: "0 0 8px ".concat(ambientColor, ", 0 14px 28px ").concat(keyColor),
    ["shadow64".concat(tokenSuffix)]: "0 0 8px ".concat(ambientColor, ", 0 32px 64px ").concat(keyColor)
  };
}